<template>
  <div class="map" id="map"></div>
</template>

<script>
export default {
  props: ['map'],
  watch: {
    map (value) {
      this.map.setMapName('map');
      this.map.load();
    }
  }
}
</script>

<style>
  .map {
    width:100vw;
    height:calc(100vh - 6rem);
  }
</style>



