<template>
  <div class="flex-container observation-item" v-bind:class="{'observation-item_online': !isOffline}">
    <div class="flex-item date"><p class="caption">{{date}}</p></div>
    <div v-on:click.prevent="onClick" class="flex-item flex-item_full info">
      <h4>{{observation.name}} <em v-if="observation.shop.name"></em></h4>
      <p class="caption" v-if="observation.shop.address">{{observation.shop.name}}, {{observation.shop.address}}</p>
      <p v-if="observation.description">{{observation.description}}</p>
      <error-list v-bind:errors="errors"></error-list>
    </div>
    <div v-on:click.prevent="onClick" class="flex-item button">
      <button v-bind:disabled="isOffline" v-bind:class="{positive: isActive}">
        {{buttonTitle}}
      </button>
    </div>
  </div>
</template>

<script>
  import _ from 'lodash'
  import db from '../../helpers/db'
  import router from '../../router/index'
  import ErrorList from '../ErrorList'

  export default {
    props: ['observation'],
    data () {
      return {
        errors: []
      }
    },
    components: {
      ErrorList
    },
    computed: {
      'date': function(){
        const d = new Date(this.observation.planned_date);
        return (d.getMonth()+1) + '/' + d.getDate() + '/' + d.getFullYear()
      },
      isActive: function(){
        return this.$store.state.observation.id == this.observation.id;
      },
      buttonTitle: function(){
        return this.isActive ? 'Active Observation' : 'Load Observation';
      },
      isOffline () {
        return !this.$store.state.is_online;
      }
    },
    methods: {
      onClick () {
        if(this.isOffline) {
          return;
        }
        (this.isActive ? this.goTo : this.load)()
      },
      goTo () {
        router.push({ name: 'map' })
      },
      load () {
        db.observation(this.observation.id, this.$store.state.auth_token).then((data) => {
          if (!data.activities.length) {
            alert('Could not activate this observation. Activities are empty.')
            return;
          }
          if (!data.quiz.quiz_questions.length) {
            alert('Could not activate this observation. Quiz is empty.')
            return;
          }
          var amountOfEmptyDropboxes = _.chain(data.quiz.quiz_questions)
            .filter((v) => {
              return v.question_type === 'options only' && _.isEqual(v.quiz_question_options, []);
            })
            .size()
            .value();
          if(amountOfEmptyDropboxes > 0){
            alert('Could not activate this observation. Some question does not have options.');
            return;
          }

          this.$store.dispatch('setObservation', data);
        });
      }
    }
  }
</script>

<style>
  .flex-container {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-content: stretch;
    align-items: flex-start;
  }

  .flex-item {
    order: 0;
    flex: 0 1 auto;
    align-self: auto;
    padding-right:1rem;
  }
  .flex-item:last-child {
    padding-right:0;
  }

  .flex-item_full {
    order: 0;
    flex: 1 1 auto;
    align-self: auto;
  }

  .observation-item {
    padding:2rem 0;
    border-top:1px solid #f4f4f4;
  }


  .observation-item .date {
    width:9em;
    flex: 0 0 9em;
  }
  .observation-item .date p {
    margin: .5rem 0 0;
  }
  .observation-item .info {

  }
  .observation-item .info h4 {
    margin: 0;
    display: inline;
  }
  .observation-item.observation-item_online .info h4 {
    color: #2e9dfd;
    text-shadow: -1px 0 #fff, 1px 0 #fff, 0 -1px #fff, 0 -2px #fff, 0 -3px #fff, 0 -4px #fff, 0 -5px #fff, 0 -6px #fff;
    background: -webkit-linear-gradient(#2e9dfd,#2e9dfd 1px,rgba(255,255,255,.9) 1px,rgba(255,255,255,.9) 2px,transparent 2px);
    background: linear-gradient(#2e9dfd, #2e9dfd 1px, rgba(255,255,255,.9) 1px,rgba(255,255,255,.9) 2px,transparent 2px);
    background-size: 1px 0.15em;
    background-position: -9px 100%;
    background-repeat: repeat-x;
  }
  .observation-item .info p {
    margin: .5rem 0 0 0;
  }
  .observation-item .button {
    margin-top: .2rem;
  }
</style>




