
function customConfirm (message, confirmCallback, title, buttonLabels){
  if (!!navigator.notification && !!navigator.notification.alert) {
    return navigator.notification.confirm.apply(null, arguments);
  } else {
    var confirmResult = confirm(arguments[0] ? arguments[0] : "");
    if (confirmResult === true) {
      if (typeof confirmCallback === "function") {
        return confirmCallback.call(null, 1);
      }
    }
  }
};

export default customConfirm



