import Vue from 'vue'
import store from '../store'
import settings from './settings'

function setLoaderOn() {
  "use strict";
  store.dispatch('setLoadingOn');
}

function setLoaderOff() {
  "use strict";
  store.dispatch('setLoadingOff');
}

function resolveData(response) {
  "use strict";
  setLoaderOff();
  return Promise.resolve(response.data);
}

function rejectError(error) {
  "use strict";
  setLoaderOff();
  return Promise.reject(error);
}

function catchInternetConnection(error) {
  "use strict";
  if(error.body === null && error.status === 0) {
    alert('Please check connection to the internet.');
  }
  return Promise.reject(error);
}

const login = function login (username, password) {
  setLoaderOn();
  return Vue.http.get(settings.baseUrl+'/login', {
    params: {
      email: username,
      password: password
    }
  })
    .then(resolveData)
    .catch(rejectError);
}

const observations = function observations (auth_token) {
  setLoaderOn();
  return Vue.http.get(settings.baseUrl+'/observations', {
    params: {
      auth_token: auth_token
    }
  })
    .then(resolveData)
    .catch(rejectError);

}

const observation = function observation (id, auth_token) {
  setLoaderOn();
  return Vue.http.get(settings.baseUrl+'/observations/' + id, {
    params: {
      auth_token: auth_token
    }
  })
    .then(resolveData)
    .then((observation) => {
      return map(id, auth_token).then((map) => {
        observation.map = map;
        return observation;
      })
    })
    .catch(rejectError);
}

const map = function map (id, auth_token) {
  setLoaderOn();
  return Vue.http.get(settings.baseUrl+'/observations/' + id + '/map', {
    params: {
      auth_token: auth_token
    }
  })
    .then(resolveData)
    .catch(rejectError);
}

const sendShopper = function sendShopper (data, auth_token) {
  const postBody = serialize({'shopper': data});

  setLoaderOn();
  return Vue.http.post(settings.baseUrl+'/observations/' + data.ob_id + '/shopper', postBody, {
    headers: {
      'Content-Type': 'multipart/form-data'
    },
    params: {
      auth_token: auth_token
    }
  })
  .then(resolveData)
  .catch(rejectError);
}


function serialize(obj, prefix = false) {
  var str = [];
  for(var p in obj) {
    var k = prefix ? prefix + "[" + p + "]" : p, v = obj[p];
    str.push(typeof v == "object" ?
      serialize(v, k) :
    encodeURIComponent(k) + "=" + encodeURIComponent(v));
  }
  return str.join("&").replace(/%20/g, '+');
};

export default { login, observations, observation, catchInternetConnection, sendShopper }



