<template>
  <div class="observation-list">
    <div v-if="observations.length">
      <button class="button__h3 light-blue" v-bind:disabled="isOffline" @click="loadObservations">Update list</button>
      <h3>Avaliable Observations</h3>

      <div class="observations" v-for="observation in observations">
        <list-item v-bind:observation="observation"></list-item>
      </div>
    </div>

    <div v-if="!observations.length">
      <p class="text-center caption">There are no observations on the device.</p>
      <button class="full-width big" @click="loadObservations">Load observations from server</button>
    </div>
  </div>
</template>

<style>
  .observation-list {
    margin:2rem 2rem;
  }

  .button__h3 {
    float: right;
    margin-top:0.6rem;
  }
</style>

<script>
  import ListItem from './ListItem'
  import db from '../../helpers/db'
  import _ from 'lodash'

  export default {
    components: {
      ListItem
    },
    data () {
      return {
        isLoading: false
      }
    },
    created () {
      this.loadObservations();
    },
    computed: {
      observations () {
        return _.orderBy(this.$store.state.observations, 'planned_date', ['desc'])
      },
      isOffline () {
        return !this.$store.state.is_online;
      }
    },
    methods: {
      loadObservations () {
        db.observations(this.$store.state.auth_token)
          .then((data) => {
            this.$store.dispatch('setObservations', data);
          })
          .catch(db.catchInternetConnection);
      }
    }
  }
</script>



