Tracker.prototype.initShopper = function(){
  "use strict";
  this.shopper = {
    id: new Date().getTime(),
    tr_id: this.trackerId,
    ob_id: this.observationId,
    status: 0,
    q_a: [],
    tr_logs: []
  };
};

Tracker.prototype.getShopper = function(){
  "use strict";
  return _.cloneDeep(this.shopper);
};


Tracker.prototype.setQuestionAnswers = function(answers){
  this.shopper.q_a = answers;
};

Tracker.prototype.addTrackerLog = function(x, y){
  var timestamp = parseInt(new Date().getTime() / 1000);
  this.shopper.tr_logs.push(
    {
      's_ts': timestamp, // seconds from UNIX timestamp
      'e_ts': timestamp,
      'x': x,
      'y': y,
      'a_logs': []
    }
  );
};

Tracker.prototype.addActivityLog = function(a_id, sh_id){
  const currentTrackPoint = _.last(this.shopper.tr_logs);
  if(!currentTrackPoint) return;

  currentTrackPoint.a_logs.push({
    'a_id': a_id,
    'sh_id': sh_id
  });
};

Tracker.prototype.removeLastEntry = function(){
  this.shopper.tr_logs = _.initial(this.shopper.tr_logs);
};

Tracker.prototype.removeLastTrackerLog = function(){
  if(this.shopper.tr_logs.length <= 1){
    return false;
  }
  const currentTrackPoint = _.last(this.shopper.tr_logs);

  if(currentTrackPoint.a_logs.length > 0){
    return customConfirm("Undo last step and related activities?", (buttonIndex) => {
      if (buttonIndex === 1) {
        this.removeLastEntry();
        return true;
      } else {
        return false;
      }
    }, "Undo last step");
  } else {
    this.removeLastEntry();
    return true;
  }
};

Tracker.prototype.getTrackerRoutes = function(limit){
  if(typeof limit === "undefined" || limit < 2){
    limit = 2;
  }
  if(this.shopper.tr_logs.length < 1){
    return false;
  }

  return _.chain(this.shopper.tr_logs)
    .takeRight(limit)
    .map(function(v){ return _.pick(v,['x', 'y']); })
    .reverse()
    .value();
};
Tracker.prototype.getTrackerMarkers = function(limit){
  if(typeof limit === "undefined" || limit < 2){
    limit = 2;
  }
  if(this.shopper.tr_logs.length < 1){
    return false;
  }

  return _.chain(this.shopper.tr_logs)
    .take()
    .union(_.takeRight(this.shopper.tr_logs, limit - 1))
    .map(function(v){ return _.pick(v,['x', 'y']); })
    .value();
};
