<template>
  <router-link v-if="observation" class="active-observation" :to="{ name: 'map' }">
    <p class="caption">Active observation</p>
    <h2>
      {{observation.name}}
    </h2>
  </router-link>
</template>

<style>
  .active-observation {
    background-color: #f4f4f4;
    padding:2rem 9rem 2rem 2rem;
    margin:2rem 0 0;
    position: relative;
    display: block;
  }
  .active-observation:after {
    content: '→';
    position: absolute;
    right: 2rem;
    font-size: 5rem;
    line-height: 1;
    top: 50%;
    margin-top:-2.9rem;
    color: #2e9dfd;
  }

  .active-observation .caption {
    margin: 0;
    padding: 0;
    line-height: 1;
  }
  .active-observation h2 {
    margin: 0;
    padding: 0;
    display: inline;
    color: #2e9dfd;
    text-shadow: -1px 0 #f4f4f4, 1px 0 #f4f4f4, 0 -1px #f4f4f4, 0 -2px #f4f4f4, 0 -3px #f4f4f4, 0 -4px #f4f4f4, 0 -5px #f4f4f4, 0 -6px #f4f4f4;
    background: -webkit-linear-gradient(#2e9dfd,#2e9dfd 1px,rgba(255,255,255,.9) 1px,rgba(255,255,255,.9) 2px,transparent 2px);
    background: linear-gradient(#2e9dfd, #2e9dfd 1px, rgba(244,244,244,.9) 1px,rgba(244,244,244,.9) 2px,transparent 2px);
    background-size: 1px 0.15em;
    background-position: -9px 100%;
    background-repeat: repeat-x;
  }

</style>

<script>
  export default {
    computed: {
      observation () {
        return this.$store.state.observation;
      }
    }
  }
</script>



