  */
import _ from 'lodash'
import db from '../../helpers/db'

export default {
  computed: {
    isOffline () {
      return !this.$store.state.is_online;
    },
    dataAmount () {
      return this.$store.state.shoppers.length
    },
    isDataExist () {
      return this.dataAmount > 0;
    }
  },
  methods: {
    sendData() {
      const shopper = _.chain(this.$store.state.shoppers).first().cloneDeep().value();
      console.log(shopper);
      if (!shopper) {
        return;
      }

      db.sendShopper(_.cloneDeep(shopper), this.$store.state.auth_token)
        .then(() => { return this.$store.dispatch('removeFirstShopper'); })
        .then(() => { this.sendData() })
        .catch(db.catchInternetConnection)
    }
  }
}
</script>

<style>
  .send-data {
    margin:2rem 2rem 0;
  }
</style>



