<template>
  <ul class="error-list" v-if="errors.length">
    <li v-for="error in errors">{{error}}</li>
  </ul>
</template>

<style>
  .error-list {
    list-style: none;
    margin: 0;
    padding: 0;
    color: #db2828;
    font-size:1.3rem;
  }
</style>

<script>
  export default {
    props: ['errors']
  }
</script>



