import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from 'vuex-persistedstate'

Vue.use(Vuex)

const state = {
  is_online: false,
  is_loading: false,
  auth_token: false,
  tracker_id: false,
  observations: [],
  observation: false,
  shoppers: []
}

const mutations = {
  AUTH_TOKEN(state, data) {
    state.auth_token = data
  },
  TRACKER_ID(state, data) {
    state.tracker_id = data
  },
  SET_OBSERVATIONS(state, data) {
    state.observations = data
  },
  SET_OBSERVATION(state, data) {
    state.observation = data
  },
  SET_LOADING_ON(state) {
    state.is_loading = true
  },
  SET_LOADING_OFF(state) {
    state.is_loading = false
  },
  SET_STATUS_ONLINE(state) {
    state.is_online = true
  },
  SET_STATUS_OFFLINE(state) {
    state.is_online = false
  },
  SAVE_SHOPPER(state, data) {
    state.shoppers.push(data)
  },
  REMOVE_FIRST_SHOPPER(state) {
    state.shoppers = _.tail(state.shoppers)
  }
}

const actions = {
  setAuth({commit}, data) {
    commit('AUTH_TOKEN', data.auth_token);
    commit('TRACKER_ID', data.tracker_id);
  },
  clearAuthToken({commit}) {
    commit('AUTH_TOKEN', false);
  },
  setObservations({commit}, data) {
    commit('SET_OBSERVATIONS', data);
  },
  setObservation({commit}, data) {
    commit('SET_OBSERVATION', data);
  },
  setLoadingOn({commit}) {
    commit('SET_LOADING_ON');
  },
  setLoadingOff({commit}) {
    commit('SET_LOADING_OFF');
  },
  setStatusOnline({commit}) {
    commit('SET_STATUS_ONLINE');
  },
  setStatusOffline({commit}) {
    commit('SET_STATUS_OFFLINE');
  },
  saveShopper({commit}, data) {
    commit('SAVE_SHOPPER', data);
  },
  removeFirstShopper({commit}) {
    commit('REMOVE_FIRST_SHOPPER');
  }
}

const store = new Vuex.Store({
  state,
  mutations,
  actions,
  plugins: [createPersistedState({
    key: 'track-n-trace'
  })]
})

export default store



