<template>
  <div class="observation">
    <app-header v-bind:title="title">
      <button v-on:click.prevent="backToList" slot="left">Back to List</button>
      <button v-on:click.prevent="toggleQuiz" slot="right">{{buttonQuizTitle}}</button>
    </app-header>
    <div class="map-view"><map-view v-bind:map="map"></map-view></div>
    <quiz v-bind:quiz="quiz" v-bind:show-quiz="showQuiz"></quiz>
    <footer class="footer">
      <div class="footer__button">
        <button class="negative" v-on:click.prevent="undoLastStep" v-bind:disabled="isTrackerLogEmpty">Undo</button>
      </div>
      <div class="footer__button">
        <button class="negative" v-on:click.prevent="resetCurrentShopper" v-bind:disabled="isTrackerLogEmpty && isQuizEmpty">Reset</button>
      </div>
      <div class="footer__button footer__button_full">
        <button class="positive full-width" v-bind:disabled="isObservationNotComplete" v-on:click.prevent="saveObservation">Complete</button>
      </div>
    </footer>
  </div>
</template>

<script>
  import router from '../router/index'
  import micello from '../scripts-map/micellomap'
  import {tMapsTheme } from '../scripts-map/map-theme'
  import customConfirm from '../helpers/customConfirm'
  import Tracker from '../helpers/tracker'
  import AppHeader from '../components/AppHeader'
  import MapView from '../components/Observation/MapView'
  import Quiz from '../components/Observation/Quiz'

/*
{
 'id': shopper.q_a.length + 1,
 'q_id': question.id,
 'c_t': question.question_type === "custom text only" ? question.answer : '',
 'c_f': question.question_type === "custom float only" ? question.answer : '',
 'q_o_id': question.question_type === "options only" ? question.answer : ''
}
 */
function getQuizAnswerKey(type) {
  switch(type) {
    case "custom text only":
      return 'c_t';
      break;
    case "custom float only":
      return 'c_f';
      break;
    case "options only":
      return 'q_o_id';
      break;
  }
}
function prepareQuizAnswers(answer) {
  const result = {
    q_id: answer.id,
    c_t: '',
    c_f: '',
    q_o_id: ''
  };
  result[getQuizAnswerKey(answer.question_type)] = answer.model;
  return result;
}

export default {
  components: {
    AppHeader,
    MapView,
    Quiz
  },
  data (){
    return {
      title: '',
      showQuiz: false,
      quiz: '',
      tracker: undefined,
      map: undefined
    }
  },
  mounted () {
    this.title = this.$store.state.observation.name;
    this.initObservation()
  },
  methods: {
    initObservation () {
      this.tracker = new Tracker(this.$store.state.tracker_id, this.$store.state.observation.id);
      this.map = new micello();
      this.map.setMapTheme(tMapsTheme);
      this.map.setMapData(this.$store.state.observation.map);
      this.map.setMapActivities(this.$store.state.observation.activities);
      this.map.setTracker(this.tracker);
    },
    clearShopperData () {
      this.tracker.initShopper();
      _.forEach(this.quiz, function(v){
        v.model = '';
      });
      this.map.load();
    },
    resetCurrentShopper () {
      customConfirm("Reset current tracking data?", (buttonIndex) => {
        if (buttonIndex === 1) {
          this.clearShopperData()
        }
      }, "Cancel current tracking");
    },
    undoLastStep () {
      if(this.tracker.removeLastTrackerLog()){
        this.map.redrawRouteMarkers();
      }
    },
    backToList () {
      if(this.isTrackerLogEmpty && this.isQuizEmpty) {
        this.clearShopperData()
        router.push({name: 'list'});
        return;
      }

      customConfirm("Are you sure? Data of current track will be lost.", (buttonIndex) => {
        if(buttonIndex == 1){
          this.clearShopperData()
          router.push({ name: 'list' })
        }
      }, "Back to List");
    },
    toggleQuiz () {
      this.showQuiz = !this.showQuiz
    },
    saveObservation () {
      this.tracker.setQuestionAnswers(_.map(this.quiz, prepareQuizAnswers));
      this.$store.dispatch('saveShopper', this.tracker.getShopper());
      this.clearShopperData();
    }
  },
  computed: {
    buttonQuizTitle () {
      return this.showQuiz ? 'Hide Quiz' : 'Show Quiz'
    },
    quiz () {
      return _.map(this.$store.state.observation.quiz.quiz_questions, function(v){
        v.model = '';
        return v;
      });
    },
    isTrackerLogEmpty () {
      if(_.has(this.tracker, 'shopper.tr_logs')){
        return this.tracker.shopper.tr_logs.length === 0;
      }
      return true;
    },
    isQuizEmpty () {
      return _.every(this.quiz, {model: ""})
    },
    isQuizNotFinished () {
      return _.some(this.quiz, {model: ""})
    },
    isObservationNotComplete () {
      return this.isTrackerLogEmpty || this.isQuizNotFinished;
    }
  }
}

</script>

<style>
  .map-view {
    width:100vw;
    height:calc(100vh - 6rem);
  }
  .footer {
    height:3rem;
    padding:4px;
    background: #f4f4f4;
    border-top:1px solid #e0e0e0;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-content: stretch;
    align-items: flex-start;
  }

  .footer__button {
    order: 0;
    flex: 1 0 auto;
    align-self: auto;
    padding-right:1rem;
  }
  .footer__button_full {
    flex: 1 1 100%;
    padding-left:10rem;
    padding-right:0rem;
  }
  @media (max-width: 600px) {
    .footer__button_full {
      padding-left:2rem;
    }
  }
</style>



